<template>
  <el-container>
    <!--  头部  -->
    <el-header>
      <Header
          @backToScreen="backToScreen"
          @menuCollapse="menuCollapse"
          :collapseFlag="collapseFlag"
          :currOptionNum="currOptionNum"
          @tabChanged="tabChanged"
      />
    </el-header>

    <el-container>
      <el-aside style="width: auto">
        <Menu
            @menuChange="menuChange"
            mode="vertical"
            :collapseFLag="collapseFlag"
        />
      </el-aside>

      <el-main>
        <keep-alive>
          <router-view :currTabNum="currTabNum"></router-view>
        </keep-alive>
      </el-main>

    </el-container>
  </el-container>
</template>

<script>
// ------------ aside -----------------
import Menu from "./aside/menu"
import Header from "./header/index"

export default {
  components: {
    Menu,
    Header
  },
  data() {
    return {
      // ---------- aside用参数 -------------
      collapseFlag: false,
      // ---------- head用参数 ----------
      currOptionNum: 1,
      // --------- main用参数 ------------
      currTabNum: 1,
      currPageNum: 1
    }
  },
  methods: {
    // --------------- 功能 ----------------

    /**
     * 导航收缩与展开
     */
    menuCollapse: function () {
      this.collapseFlag = !this.collapseFlag
    },

    /**
     * 导航变化
     * @param val
     */
    menuChange: function (val) {
      this.currOptionNum = Number(val);
      this.currPageNum = Number(val);
      this.currTabNum = 1;
      // if (val === "blackBroad") {
      //   location.replace("#/blackBroad");
      //   location.reload();
      // }
      // if (val === "bigData") {
      //   location.replace("#/bigData");
      //   location.reload();
      // }
    },

    /**
     * 标签页发生变化
     * @param val
     */
    tabChanged: function (val) {
      this.currTabNum = Number(val)
    },

    /**
     * 返回大屏
     */
    backToScreen: function () {
      this.$router.push({name: "index"})
    }
  }
}
</script>

